.modal-menu-phone {
  display: none;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4352941176);
  top: 0;
}
@media (min-width: 768px) {
  .modal-menu-phone {
    display: none;
  }
}
.modal-menu-phone .nav-phone {
  display: flex;
  flex-direction: column;
  width: 70%;
  padding: 2rem;
  height: 100%;
  background-color: #FFFFFF;
  gap: 2rem;
  margin-right: auto;
  transition: left 0.3s ease-out, opacity 0.5s linear;
  position: absolute;
  left: -80rem;
}
