header h1 {
  color: #FFFFFF;
  margin: 0;
}
header .modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 2;
  background-color: #000;
  opacity: 0.95;
  transition: all 0.3s ease-out;
}
header .modal .close {
  display: flex;
  width: 70%;
  position: absolute;
  top: 10rem;
}
header .modal .close button {
  font-size: 2rem;
  color: #FFFFFF;
  margin-left: auto;
}
header .modal .nav__link {
  width: 70%;
}
header .modal .nav__link li {
  display: block;
  transition: all 0.3s ease-out;
  border-radius: 0.3rem;
}
header .modal .nav__link li a {
  display: block;
  text-align: center;
  font-weight: 700;
  color: #FFFFFF;
  padding: 2rem;
}
header .modal .nav__link li:hover {
  background-color: rgba(255, 255, 255, 0.3882352941);
}
header .modal.off {
  opacity: 0;
  z-index: -1;
}
header #menu {
  cursor: pointer;
}
header .splide .splide__track {
  height: 60rem;
}
header .splide .splide__track .splide__list .splide__slide {
  background-size: cover;
  background-repeat: no-repeat;
}
header .splide .splide__track .splide__list .splide__slide.slide-1 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)), url("/build/img/splide2.jpeg");
  background-position: center center;
}
header .splide .splide__track .splide__list .splide__slide.slide-2 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)), url("/build/img/splide1.jpeg");
}
header .splide .splide__track .splide__list .splide__slide.slide-3 {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.3)), url("/build/img/splide3.jpeg");
  background-position: right;
}
header.absolute .nav {
  position: absolute;
}
header.relative {
  background-color: #00324D;
  padding: 2rem 0;
}
header.relative .nav {
  position: relative;
  margin: 0 auto;
}
