.formularios {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.formularios .select__form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 0;
}
.formularios .select__form .select__categorias {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1rem;
}
.formularios .select__form .select__categorias .categoria label {
  display: block;
  padding: 1rem;
  border-radius: 0.5rem;
  background-color: #FFFFFF;
  border: 1px solid #000;
  text-align: center;
  font-weight: 700;
  transition: all 0.3s ease-out;
}
.formularios .select__form .select__categorias .categoria label:hover {
  cursor: pointer;
  background-color: #000;
  color: #FFFFFF;
}
.formularios .select__form .select__categorias .categoria input[type=radio] {
  display: none;
}
@media (min-width: 768px) {
  .formularios .select__form .select__categorias {
    grid-template-columns: repeat(2, 1fr);
  }
}
.formularios h1 {
  margin-bottom: 0;
}
.formularios .categorias .title-categorias .descargar-formato {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.formularios .categorias .title-categorias .descargar-formato p {
  font-size: 1.75rem;
  text-align: center;
}
.formularios .categorias .title-categorias .descargar-formato button {
  margin: 0;
}
.formularios .categorias .formulario {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.formularios .categorias .formulario .form {
  display: flex;
  flex-direction: column;
}
.formularios .categorias .formulario .form fieldset {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  gap: 1rem;
}
.formularios .categorias .formulario .form fieldset legend {
  font-weight: 700;
}
.formularios .categorias .formulario .form fieldset .coolinput .add-participante {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .formularios .categorias .formulario .form fieldset .coolinput .add-participante {
    flex-direction: row;
    align-items: center;
  }
}
.formularios .categorias .formulario .form fieldset .coolinput .add-participante input[type=text] {
  flex: 3;
}
.formularios .categorias .formulario .form fieldset .coolinput .add-participante button {
  margin: 0;
}
.formularios .categorias .formulario .form fieldset .coolinput .group-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.formularios .categorias .formulario .form fieldset .coolinput .group-list .list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .formularios .categorias .formulario .form fieldset .coolinput .group-list .list {
    flex-direction: row;
  }
}
.formularios .categorias .formulario .form fieldset .coolinput .group-list .list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  list-style: none;
  padding: 1rem 1.5rem;
  border-radius: 0.5rem;
  background-color: #f7f7f7;
  font-size: 1.2rem;
  width: 100%;
}
.formularios .categorias .formulario .form fieldset .coolinput .group-list .list li button {
  width: 2rem;
  height: 2rem;
  background-color: #CB7766;
  border: none;
  color: #FFFFFF;
  font-weight: 700;
  border-radius: 100%;
  transition: background-color 0.3s ease-out;
  text-align: center;
}
.formularios .categorias .formulario .form fieldset .coolinput .group-list .list li button:hover {
  cursor: pointer;
  background-color: #c56653;
}
.formularios .categorias .formulario .form fieldset.fieldset-contacto {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.formularios .categorias .formulario .form fieldset.fieldset-contacto .participant h4 {
  text-align: center;
}
.formularios .categorias .formulario .form fieldset.fieldset-contacto .participant .field {
  margin-bottom: 1rem;
}
.formularios .categorias .formulario .form input[type=submit] {
  padding: 1.5rem;
}